@import url(https://fonts.googleapis.com/css?family=Lato:300);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", sans-serif;
  color: #222;
  margin: 0.4em 0; }

h1 {
  font-size: calc(1rem + 1.5vw); }

h2 {
  font-size: calc(0.9rem + 1.2vw); }

h3 {
  font-size: calc(0.8rem + 1.0vw); }

h4 {
  font-size: calc(0.7rem + 0.9vw); }

h5 {
  font-size: calc(0.6rem + 0.8vw); }

h6 {
  font-size: calc(0.5rem + 0.6vw); }

p {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px; }

.p-xs {
  font-size: 14px; }

.form-control {
  font-family: "Source Sans Pro", sans-serif;
  font-size: calc(0.7rem + 0.6vw); }

.navbar-container img {
  max-height: 40px; }

.left-col-container .toggle-ico {
  vertical-align: middle;
  max-width: 15px; }

.left-col-container .shipping-methods-container {
  margin-top: 25px; }
  .left-col-container .shipping-methods-container p {
    font-size: 14px; }

.left-col-container .payment-btn-group {
  text-align: center; }
  .left-col-container .payment-btn-group button {
    margin: 4px 2px;
    padding: 0.5rem 0.4rem;
    font-size: 14px; }
  .left-col-container .payment-btn-group .box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 1em 0; }
  .left-col-container .payment-btn-group .paypal-logo {
    font-family: Verdana, Tahoma;
    font-weight: bold;
    font-size: 26px; }
    .left-col-container .payment-btn-group .paypal-logo i:first-child {
      color: #253b80; }
    .left-col-container .payment-btn-group .paypal-logo i:last-child {
      color: #179bd7; }
  .left-col-container .payment-btn-group .paypal-button {
    padding: 15px 15px;
    border: 1px solid #FF9933;
    border-radius: 5px;
    background-image: -webkit-linear-gradient(#FFF0A8, #F9B421);
    background-image: linear-gradient(#FFF0A8, #F9B421);
    margin: 0 auto;
    display: block;
    min-width: 138px;
    position: relative; }
    .left-col-container .payment-btn-group .paypal-button-title {
      font-size: 14px;
      color: #505050;
      vertical-align: baseline;
      text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.6); }
    .left-col-container .payment-btn-group .paypal-button .paypal-logo {
      display: inline-block;
      text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.6);
      font-size: 20px; }

.left-col-container .sc-bdVaJa {
  width: 100%; }

.left-col-container .sc-bwzfXH {
  border: 1px solid #ced4da; }

.right-col-container {
  background-color: #eee; }
  .right-col-container .cart-details-container h5, .right-col-container .cart-details-container h6, .right-col-container .cart-details-container p, .right-col-container .cart-details-container a {
    font-size: 14px; }
  .right-col-container .cart-details-container .media {
    position: relative;
    max-width: 3.5em; }
  .right-col-container .cart-details-container .media-count {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #00d2d3;
    color: #fff;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 4px 4px 0px; }
  .right-col-container .inforeel-container p {
    font-size: 0.9em; }
  .right-col-container .inforeel-container .title-p {
    margin-bottom: 0; }
  .right-col-container .inforeel-container .co-badge-container {
    max-width: 80px;
    padding: 0; }
    .right-col-container .inforeel-container .co-badge-container img {
      margin-top: 10px;
      width: 100%; }
  .right-col-container .review-container .star-rate {
    display: block;
    margin: -4px 0; }
  .right-col-container .review-container .fa-star {
    color: #FFC107;
    font-size: 0.8em; }
  .right-col-container .review-container .co-verified-buyer span {
    font-size: 0.8em; }
  .right-col-container .avatar-container {
    max-width: 150px; }

.footer {
  width: 100%;
  height: 60px;
  line-height: 60px;
  background-color: #f5f5f5; }
  .footer p {
    margin-bottom: 0; }

